import type { StorageBackend } from './StateStorage';

export class ProxyStorageBackend<T> implements StorageBackend<T> {
	private readonly isProxyBackend = true;

	private constructor(
		private readonly backend: StorageBackend<any>,
		private readonly path: (keyof any)[]
	) {}


	public get(): T {
		let current = this.backend.get();
		let index = 0;

		while (index < this.path.length) {
			if (typeof current !== 'object' || current === null) {
				throw new Error('Primitive types cannot hold sub-states.');
			}

			const key = this.path[index++];
			if (typeof key !== 'number' && Array.isArray(current)) {
				throw new Error('Cannot access array sub-state under non-numeric keys.');
			}

			current = current[key];
		}

		return current;
	}

	public set(state: T, immediate?: boolean | undefined) {
		let current = this.backend.get();
		let index = 0;

		const root = Array.isArray(current) ? current.slice() : { ...current };
		current = root;

		while (index < this.path.length) {
			if (typeof current !== 'object' || current === null) {
				throw new Error('Primitive types cannot hold sub-states.');
			}

			const key = this.path[index++];
			if (typeof key !== 'number' && Array.isArray(current)) {
				throw new Error('Cannot access array sub-state under non-numeric keys.');
			}

			if (index === this.path.length) {
				current[key] = state;
			}
			else {
				const next = current[key];
				const clone = Array.isArray(next) ? next.slice() : { ...next };
				current[key] = clone;
				current = clone;
			}
		}

		this.backend.set(root, immediate);
	}


	public static proxySubState<TState, TKey extends keyof TState>(
		backend: StorageBackend<TState>,
		key: TKey
	): ProxyStorageBackend<TState[TKey]> {
		if (ProxyStorageBackend.isProxy(backend)) {
			return new ProxyStorageBackend(
				backend.backend,
				backend.path.concat(key)
			);
		}

		return new ProxyStorageBackend(backend, [ key ]);
	}

	private static isProxy(backend: StorageBackend<any>): backend is ProxyStorageBackend<unknown> {
		return (backend as ProxyStorageBackend<any>).isProxyBackend;
	}
}
