import type { Attribute, Attributes } from '~/game/entities/Attributes';
import { AppliedEffect, Effect, MutableAppliedEffect } from '~/game/entities/combat/Effect';

import { Combatant, CombatSide, SetMode } from './Combatant';
import { CombatEventKind } from './CombatEvent';
import type { LivingEntity } from './LivingEntity';
import type { OngoingCombatSimulator } from './types';

export class CombatParticipant implements Combatant {
	private _effects: MutableAppliedEffect[] = [];
	private attributeOverrides: Attributes = {};

	public constructor(
		private readonly simulator: OngoingCombatSimulator,
		public readonly entity: LivingEntity,
		public readonly side: CombatSide
	) {}

	public get effects(): readonly AppliedEffect[] {
		return this._effects;
	}

	public get health() {
		return this.entity.health.value;
	}

	public get isAlive() {
		return this.entity.health.isAlive;
	}

	public damage(value: number, defenseAttribute?: Attribute) {
		const defense = defenseAttribute === undefined ? 0 : this.getAttribute(defenseAttribute);

		const adjusted = Math.max(value, 0) * (1 / (1 + defense / 11));
		const change = this.setHealth(-adjusted);
		if (change === 0) {
			return;
		}

		this.simulator.report({
			kind: CombatEventKind.DIRECT_DAMAGE,
			healthChange: change,
			source: this.simulator.currentAttacker,
			target: this
		});

		if (!this.isAlive) {
			this._effects = [];
			this.simulator.report({
				kind: CombatEventKind.PARTICIPANT_DIED,
				target: this
			});
		}
	}

	public heal(value: number) {
		const isRevive = !this.isAlive;
		const change = this.setHealth(Math.max(value, 0));
		if (change === 0) {
			return;
		}

		this.simulator.report({
			kind: CombatEventKind.DIRECT_HEAL,
			healthChange: change,
			source: this.simulator.currentAttacker,
			target: this
		});

		if (isRevive) {
			this.simulator.report({
				kind: CombatEventKind.PARTICIPANT_REVIVED,
				revivalHealth: change,
				target: this
			});
		}
	}

	public getAttribute(attribute: Attribute) {
		return this.attributeOverrides[attribute] ?? this.entity.getAttribute(attribute);
	}

	public setAttribute(attribute: Attribute, value: number, mode: SetMode = SetMode.CONST) {
		let override;
		switch (mode) {
			case SetMode.CONST:
				override = value;
				break;

			case SetMode.OFFSET:
				override = this.getAttribute(attribute) + value;
				break;

			case SetMode.MULTIPLIER:
				override = this.getAttribute(attribute) * value;
				break;
		}

		this.attributeOverrides[attribute] = override;
	}

	public addEffect(effect: Effect, param = 0) {
		if (!this.isAlive) {
			return;
		}

		// try increase stack if enabled
		if (effect.maxStackCount > 0) {
			const applied = this._effects.find(e => (
				e.effectKey === effect.key &&
				e.source === this.simulator.currentAttacker
			));

			if (applied) {
				applied.remainingDuration = effect.duration;
				applied.stackCount = Math.min(applied.stackCount + 1, effect.maxStackCount);
				this.simulator.report({
					kind: CombatEventKind.EFFECT_STACKED,
					effectKey: effect.key,
					source: this.simulator.currentAttacker,
					target: this,
					stackCount: applied.stackCount
				});

				return;
			}
		}

		this._effects.push({
			effectKey: effect.key,
			param,
			source: this.simulator.currentAttacker,
			remainingDuration: effect.duration,
			stackCount: 1
		});

		this.simulator.report({
			kind: CombatEventKind.EFFECT_APPLIED,
			effectKey: effect.key,
			source: this.simulator.currentAttacker,
			target: this
		});
	}

	public removeEffect(effect: AppliedEffect) {
		const index = this._effects.indexOf(effect);
		if (index !== -1) {
			const existing = this._effects[index];
			this._effects.splice(index, 1);
			this.simulator.report({
				kind: CombatEventKind.EFFECT_REMOVED,
				effectKey: existing.effectKey,
				target: this
			});
		}
	}

	public tickEffects() {
		this.attributeOverrides = {};

		let i = 0;
		while (i < this._effects.length) {
			const applied = this._effects[i];
			if (applied.remainingDuration === 0) {
				this._effects.splice(i, 1);
				this.simulator.report({
					kind: CombatEventKind.EFFECT_FADED,
					effectKey: applied.effectKey,
					target: this
				});

				continue;
			}

			--applied.remainingDuration;
			this.simulator.runEffect(this, applied);
			++i;
		}
	}

	private setHealth(offset: number) {
		const oldHealth = this.entity.health.value;
		this.entity.health.value = oldHealth + offset;

		// the underlying health property has setter logic, re-read the value after setting
		const newHealth = this.entity.health.value;
		const healthChange = newHealth - oldHealth;
		if (Math.abs(healthChange) < Number.EPSILON) {
			return 0;
		}

		return healthChange;
	}
}
