import { ComponentChild, ComponentChildren, h } from 'preact';

import { bem, cx } from '~/utils/style';

import './Menu.css';

export enum Orientation {
	Horizontal = 'horizontal',
	Vertical = 'vertical'
}

export interface MenuItemRenderer<T> {
	(item: T, index: number): ComponentChild;
}

export interface MenuProps<T> {
	readonly class?: string;
	readonly footer?: ComponentChildren;
	readonly header?: ComponentChildren;
	readonly itemRenderer: MenuItemRenderer<T>;
	readonly items: readonly T[];
	readonly orientation?: Orientation;
	readonly uniform?: boolean;
}

export const Menu = <T,>({
	class: customClass,
	footer,
	header,
	itemRenderer,
	items,
	orientation = Orientation.Vertical,
	uniform = false
}: MenuProps<T>) => {
	const classes = cx(
		bem('menu', {
			horizontal: orientation === Orientation.Horizontal,
			vertical: orientation === Orientation.Vertical,
			uniform
		}),
		customClass
	);

	return (
		<div class={classes}>
			{header}
			{items.map(itemRenderer)}
			{footer}
		</div>
	);
};
