import * as Signal from '@calmdownval/signal';

export interface TimeSource {
	now(): number;
}

export interface ClockChangedEvent {
	readonly oldTime: number;
	readonly newTime: number;
}

export class Clock {
	public readonly changed = Signal.create<ClockChangedEvent>();

	private offset: number;

	public constructor(
		public readonly timeSource: TimeSource = performance
	) {
		// set the correct time initially...
		this.offset = Date.now() - timeSource.now();
	}


	public get now() {
		return this.offset + this.timeSource.now();
	}

	public set now(newTime: number) {
		const now = this.timeSource.now();
		const oldTime = this.offset + now;

		this.offset = newTime - now;
		this.changed({
			oldTime,
			newTime
		});
	}
}
