/**
 * Iterates the given array and returns the sum of values selected by the
 * provided selector.
 */
export function sum<T>(iterable: Iterable<T>, selector: (item: T) => number): number {
	let result = 0;
	for (const item of iterable) {
		result += selector(item);
	}

	return result;
}

export function min<T>(iterable: Iterable<T>, selector: (item: T) => number): number {
	let result = Infinity;
	for (const item of iterable) {
		result = Math.min(result, selector(item));
	}

	return result;
}

export function times<T>(n: number, fn: (index: number) => T): T[] {
	const items = new Array<T>(n);
	for (let i = 0; i < n; ++i) {
		items[i] = fn(i);
	}

	return items;
}

export function first<T>(array: readonly T[]): T | undefined;
export function first<T>(array: readonly T[], count: number): T[];
export function first<T>(array: readonly T[], count?: number): T[] | T | undefined {
	return count === undefined
		? array[0]
		: array.slice(0, Math.max(count, 0));
}

export function last<T>(array: readonly T[]): T | undefined;
export function last<T>(array: readonly T[], count: number): T[];
export function last<T>(array: readonly T[], count?: number): T[] | T | undefined {
	return count === undefined
		? array[array.length - 1]
		: array.slice(-Math.max(count, 0));
}
