import { useIntl } from '@calmdownval/intl-preact';
import { h } from 'preact';

import { useRouter } from '~/components/AppRouter';
import { EntryCard } from '~/components/EntryCard';
import { FlatLayout } from '~/components/FlatLayout';
import { Menu } from '~/components/Menu';
import { ProgressBar } from '~/components/ProgressBar';
import { MainHeader } from '~/components/Typography';
import { Hero } from '~/game/entities/Hero';
import { ItemKinds } from '~/game/items/ItemKind';
import { useForceUpdate } from '~/hooks/useForceUpdate';
import { enumGet } from '~/utils/enum';

import { ScreenType } from '../ScreenType';

interface HeroHealScreenParams {
	readonly hero: Hero;
}

export const HeroHealScreen: ScreenType = ({ game }) => {
	const { t } = useIntl();
	const router = useRouter<HeroHealScreenParams>();
	const { hero } = router.params;

	const header = (
		<header class='hero-heal__header'>
			<MainHeader>
				{t('ui.title.hero-heal')}
				<br />
				{t(`entity.${hero.key}`)}
			</MainHeader>
		</header>
	);

	const available = game.inventory.contents.filter(stack => enumGet(ItemKinds, stack.itemKey).healValue !== undefined);
	const forceUpdate = useForceUpdate();

	return (
		<FlatLayout>
			<EntryCard
				icon={hero.icon}
			>
				<ProgressBar
					value={hero.health.value}
					maxValue={hero.health.maxHealth}
					title={t('ui.label.hp')}
				/>
			</EntryCard>
			<Menu
				header={header}
				items={available}
				itemRenderer={stack => (
					<EntryCard
						icon={enumGet(ItemKinds, stack.itemKey).icon}
						title={t(`item.${stack.itemKey}`)}
						quantity={stack.quantity}
						action={() => {
							if (hero.health.value !== hero.health.maxHealth) {
								hero.health.value += enumGet(ItemKinds, stack.itemKey).healValue!;
								game.inventory.remove([
									{
										itemKey: stack.itemKey,
										quantity: 1
									}
								]);
								forceUpdate();
							}
						}}
					/>
				)}
			/>
		</FlatLayout>
	);
};
