import { Registry, RegistryEntry } from '~/game/Registry';
import { NoOpStorageBackend } from '~/game/storage/NoOpStorageBackend';
import { StateStorage } from '~/game/storage/StateStorage';

import type { Attribute, Attributes } from './Attributes';
import { AttackRotation } from './combat/Attack';
import type { LivingEntity } from './combat/LivingEntity';
import { Health } from './Health';

export interface EnemyInfo extends RegistryEntry {
	readonly attributes: Attributes;
	readonly icon: string;
	readonly rotation: AttackRotation;
}

export class Enemy implements LivingEntity {
	public readonly health: Health;

	private readonly info: EnemyInfo;

	public constructor(
		public readonly key: string
	) {
		this.info = Enemy.registry.get(key);
		this.health = new Health(
			this,
			new StateStorage(
				new NoOpStorageBackend(0)
			)
		);
	}

	public get rotation() {
		return this.info.rotation;
	}

	public getAttribute(attribute: Attribute) {
		return this.info.attributes[attribute] ?? 0;
	}


	public static readonly registry = new Registry<EnemyInfo>();
}
