import { PlayerError, PlayerErrorKind } from '~/game/PlayerError';
import { State } from '~/game/storage/State';
import { sum } from '~/utils/array';
import { enumGet } from '~/utils/enum';

import { ItemKinds } from './ItemKind';
import type { ItemStack } from './ItemStack';

export interface InventoryState {
	readonly capacity: number;
	readonly contents: readonly ItemStack[];
}

export class Inventory {
	public constructor(
		private readonly state: State<InventoryState>
	) {}

	public get capacity() {
		return this.state.current.capacity;
	}

	public set capacity(value: number) {
		if (value < this.state.current.contents.length) {
			throw new Error('Cannot reduce capacity below currently occupied space.');
		}

		this.state.patch({
			capacity: value
		});
	}

	public get contents(): readonly ItemStack[] {
		return this.state.current.contents;
	}

	public add(items: readonly ItemStack[]) {
		const contents = this.contents.slice();
		for (const item of items) {
			const kind = enumGet(ItemKinds, item.itemKey);

			let remaining = item.quantity;
			for (let i = 0; i < contents.length && remaining > 0; ++i) {
				const slot = contents[i];
				if (item.itemKey !== slot.itemKey) {
					continue;
				}

				const toAdd = Math.min(remaining, kind.maxStack - slot.quantity);
				contents[i] = {
					...slot,
					quantity: slot.quantity + toAdd
				};

				remaining -= toAdd;
			}

			while (remaining > 0) {
				if (contents.length === this.capacity) {
					throw new PlayerError(PlayerErrorKind.NOT_ENOUGH_INVENTORY_SPACE);
				}

				contents.push({
					itemKey: item.itemKey,
					quantity: Math.min(remaining, kind.maxStack)
				});

				remaining -= kind.maxStack;
			}
		}

		this.state.patch({ contents });
	}

	public remove(items: readonly ItemStack[]) {
		const contents = this.contents.slice();
		for (const item of items) {
			let remaining = item.quantity;
			for (let i = contents.length - 1; i >= 0 && remaining > 0; --i) {
				const slot = contents[i];
				if (item.itemKey !== slot.itemKey) {
					continue;
				}

				if (slot.quantity <= remaining) {
					contents.splice(i, 1);
					remaining -= slot.quantity;
				}
				else {
					contents[i] = {
						...slot,
						quantity: slot.quantity - remaining
					};

					remaining = 0;
				}
			}

			if (remaining > 0) {
				throw new PlayerError(PlayerErrorKind.MISSING_REQUIRED_ITEMS);
			}
		}

		this.state.patch({ contents });
	}

	public count(itemKey: string) {
		return sum(this.contents, slot => slot.itemKey === itemKey ? slot.quantity : 0);
	}
}
