import { useEffect } from 'preact/hooks';

import { Scheduler } from '~/game/time/Scheduler';

import { useForceUpdate } from './useForceUpdate';

export function useScheduledUpdates(scheduler: Scheduler, timestamps: readonly number[]) {
	const forceUpdate = useForceUpdate();
	useEffect(() => {
		if (timestamps.length === 0) {
			return undefined;
		}

		const handle = scheduler.scheduleCallbackAt(forceUpdate, timestamps);
		return () => scheduler.clear(handle);
	}, timestamps);
}
