import { ProxyStorageBackend } from './ProxyStorageBackend';
import type { State } from './State';

export interface StorageBackend<T> {
	get(): T;
	set(state: T, immediate?: boolean): void;
}

export class StateStorage<T> implements State<T> {
	public constructor(
		private readonly backend: StorageBackend<T>
	) {}

	public get current() {
		return this.backend.get()!;
	}

	public init(state: T) {
		try {
			// get throws when no state exists
			this.backend.get();
			return false;
		}
		catch (_ex) {
			this.set(state, true);
			return true;
		}
	}

	public set(state: T, immediate?: boolean) {
		this.backend.set(state, immediate);
	}

	public patch(patch: Partial<T>, immediate?: boolean) {
		this.set({
			...this.current,
			...patch
		}, immediate);
	}

	public subState<TKey extends keyof T>(key: TKey): StateStorage<T[TKey]> {
		return new StateStorage(
			ProxyStorageBackend.proxySubState(this.backend, key)
		);
	}
}
