import { useIntl } from '@calmdownval/intl-preact';
import { h } from 'preact';

import { useRouter } from '~/components/AppRouter';
import { EntryCard } from '~/components/EntryCard';
import { FlatLayout } from '~/components/FlatLayout';
import { MainHeader } from '~/components/Typography';
import { Hero } from '~/game/entities/Hero';
import * as Route from '~/routes';

import { ScreenType } from '../ScreenType';

interface HeroDeathScreenParams {
	readonly hero: Hero;
}

export const HeroDeathScreen: ScreenType = () => {
	const { t } = useIntl();
	const router = useRouter<HeroDeathScreenParams>();
	const { hero } = router.params;

	return (
		<FlatLayout>
			<MainHeader>
				{t('ui.title.hero-death')}
			</MainHeader>
			<EntryCard
				icon={hero.icon}
				title={t(`entity.${hero.key}`)}
				action={() => {
					hero.revive();
					router.navigate(Route.HEROES);
				}}
			/>
		</FlatLayout>
	);
};
