import { useIntl } from '@calmdownval/intl-preact';
import { Fragment, h } from 'preact';

import { ProgressBar } from '~/components/ProgressBar';
import { MainHeader, SubHeader } from '~/components/Typography';
import { Attribute } from '~/game/entities/Attributes';
import type { Enemy } from '~/game/entities/Enemy';
import type { Hero } from '~/game/entities/Hero';
import { tsEnumMap } from '~/utils/enum';
import { cx } from '~/utils/style';

import './EntityDetail.css';

export interface EntityDetailProps {
	readonly class?: string;
	readonly entity: Hero | Enemy;
}

export const EntityDetail = (props: EntityDetailProps) => {
	const { t } = useIntl();
	const { entity } = props;
	return (
		<div class={cx('entity-detail', props.class)}>
			<MainHeader>
				{t(`entity.${entity.key}`)}
			</MainHeader>
			<ProgressBar
				maxValue={entity.health.maxHealth}
				value={entity.health.value}
				title={t('ui.label.hp')}
			/>

			<SubHeader>
				{t('ui.label.stats')}
			</SubHeader>
			<dl class='entity-detail__attributes'>
				{tsEnumMap(Attribute, (attribute, key) => (
					<>
						<dt>
							{t(`attribute.${key}`)}
						</dt>
						<dd>
							{entity.getAttribute(attribute)}
						</dd>
					</>
				))}
			</dl>

			<SubHeader>
				{t('ui.label.rotation')}
			</SubHeader>
			<ol class='entity-detail__rotation'>
				{entity.rotation.map((attackKey, index) => (
					<li key={index}>
						<em>
							{t(`attack.${attackKey}`)}
						</em>
					</li>
				))}
			</ol>
		</div>
	);
};
