import { Attribute } from '~/game/entities/Attributes';
import { enumOf, EnumType } from '~/utils/enum';

import { Combatant, SetMode } from './Combatant';

export enum EffectKind {
	POSITIVE,
	NEGATIVE
}

export interface Effect extends EnumType {
	readonly duration: number;
	readonly kind: EffectKind;
	readonly maxStackCount: number;
	tick(this: AppliedEffect, affected: Combatant): void;
}

export interface MutableAppliedEffect {
	readonly effectKey: string;
	readonly source: Combatant;
	readonly param: number;
	remainingDuration: number;
	stackCount: number;
}

export type AppliedEffect = Readonly<MutableAppliedEffect>;

export const Effects = enumOf<Effect>()({
	SUNDER: {
		kind: EffectKind.NEGATIVE,
		duration: 3,
		maxStackCount: 5,
		tick(affected) {
			affected.setAttribute(Attribute.PHYSICAL_DEFENSE, 0.93 ** this.stackCount, SetMode.MULTIPLIER);
		}
	},
	IGNITE: {
		// does 50% of input damage (specified via param) spread over 3 turns
		kind: EffectKind.NEGATIVE,
		duration: 3,
		maxStackCount: 0,
		tick(affected) {
			affected.damage(this.param / 6);
		}
	}
});
