export enum PlayerErrorKind {
	// recruitment
	RECRUITMENT_LIMIT_REACHED,
	CRAFTER_ALREADY_RECRUITED,

	// inventory
	NOT_ENOUGH_INVENTORY_SPACE,
	MISSING_REQUIRED_ITEMS
}

export class PlayerError extends Error {
	private readonly isPlayerError = true;

	public constructor(
		public readonly kind: PlayerErrorKind,
		message?: string
	) {
		super(`${PlayerErrorKind[kind]}${message ? `: ${message}` : ''}`);
	}

	public static isPlayerError(ex: unknown): ex is PlayerError {
		return typeof ex === 'object' && ex !== null && (ex as PlayerError).isPlayerError;
	}
}
