import { useIntl } from '@calmdownval/intl-preact';
import { h } from 'preact';

import { useRouter } from '~/components/AppRouter';
import { Button } from '~/components/Button';
import { ButtonGroup } from '~/components/ButtonGroup';
import { FlatLayout } from '~/components/FlatLayout';
import { ItemGrid } from '~/components/ItemGrid';
import { MainHeader } from '~/components/Typography';
import { Hero, HeroQuestReadyActivity } from '~/game/entities/Hero';
import * as Route from '~/routes';
import type { ScreenType } from '~/screens/ScreenType';

interface QuestEndScreenParams {
	readonly hero: Hero;
}

export const QuestEndScreen: ScreenType = () => {
	const { t } = useIntl();
	const router = useRouter<QuestEndScreenParams>();
	const { hero } = router.params;
	const { loot } = hero.activity as HeroQuestReadyActivity;

	return (
		<FlatLayout>
			<MainHeader>
				{t('ui.title.quest-loot')}
			</MainHeader>
			<ItemGrid content={loot} />
			<ButtonGroup>
				<Button
					onClick={() => {
						hero.deliverQuest();
						router.navigate(Route.HEROES);
					}}
				>
					{t('ui.label.end-quest')}
				</Button>
			</ButtonGroup>
		</FlatLayout>
	);
};
