import type { State } from '~/game/storage/State';
import { clamp } from '~/utils/math';

import { Attribute } from './Attributes';
import type { LivingEntity } from './combat/LivingEntity';

export class Health {
	public constructor(
		private readonly owner: LivingEntity,
		private readonly state: State<number>
	) {}


	public get value() {
		return this.maxHealth - this.state.current;
	}

	public set value(value: number) {
		this.state.set(
			clamp(this.maxHealth - value, 0, this.maxHealth)
		);
	}

	public get isAlive() {
		return this.value > 0;
	}

	public get maxHealth() {
		return 100 + this.owner.getAttribute(Attribute.STAMINA) * 5;
	}


	public reset() {
		this.state.set(0);
	}
}
