import { h } from 'preact';

import { EntryCard } from '~/components/EntryCard';
import { ProgressBar } from '~/components/ProgressBar';
import { SubHeader } from '~/components/Typography';
import { HeroDeadActivity } from '~/game/entities/Hero';
import * as Route from '~/routes';

import type { HeroEntryRenderer } from './types';

export const HeroDeadEntryRenderer: HeroEntryRenderer<HeroDeadActivity> = (_activity, { hero, router, t, ...rest }) => (
	<EntryCard
		{...rest}
		icon=''
		action={() => router.navigate(Route.HERO_DEATH, { hero })}
	>
		<ProgressBar
			value={0}
			maxValue={hero.health.maxHealth}
			title={t('ui.label.hp')}
		/>
		<SubHeader>
			{t('ui.hero-state.died')}
		</SubHeader>
	</EntryCard>
);
