import { Equipment, equipmentSlotKeys } from '~/game/items/EquipmentSlot';
import { ItemKinds } from '~/game/items/ItemKind';
import type { ItemStack } from '~/game/items/ItemStack';
import { LootTable } from '~/game/items/LootTable';
import { questRegistry } from '~/game/quests/Quest';
import { Registry, RegistryEntry } from '~/game/Registry';
import type { State } from '~/game/storage/State';
import type { TraderGame } from '~/game/TraderGame';
import { enumGet } from '~/utils/enum';

import type { Attribute, Attributes } from './Attributes';
import { AttackRotation } from './combat/Attack';
import { CombatSide } from './combat/Combatant';
import { CombatEvent } from './combat/CombatEvent';
import { ScopedCombatSimulator } from './combat/CombatSimulator';
import type { LivingEntity } from './combat/LivingEntity';
import { Enemy } from './Enemy';
import { Health } from './Health';
import { Level } from './Level';


export enum HeroActivity {
	IDLE,
	DEAD,
	QUEST_PENDING,
	QUEST_READY
}

export interface HeroActivityBase<TKind extends HeroActivity> {
	readonly kind: TKind;
}

export interface HeroIdleActivity extends HeroActivityBase<HeroActivity.IDLE> {}

export interface HeroDeadActivity extends HeroActivityBase<HeroActivity.DEAD> {
	readonly combatEvents: readonly CombatEvent[];
}

export interface HeroQuestPendingActivity extends HeroActivityBase<HeroActivity.QUEST_PENDING> {
	readonly durationMs: number;
	readonly elapsesAt: number;
	readonly questKey: string;
}

export interface HeroQuestReadyActivity extends HeroActivityBase<HeroActivity.QUEST_READY> {
	readonly combatEvents: readonly CombatEvent[];
	readonly loot: readonly ItemStack[];
	readonly xp: number;
}


export interface HeroInfo extends RegistryEntry {
	readonly baseAttributes: Attributes;
	readonly icon: string;
	readonly levelAdvancement: Attributes;
	readonly rotation: AttackRotation;
	readonly startEquipment: Equipment;
}

export interface HeroState {
	readonly equipment: Equipment;
	readonly healthDeficit: number;
	readonly key: string;
	readonly rotation: AttackRotation;
	readonly xp: number;
	readonly activity:
	| HeroIdleActivity
	| HeroDeadActivity
	| HeroQuestPendingActivity
	| HeroQuestReadyActivity;
}


export class Hero implements LivingEntity {
	public readonly health: Health;
	public readonly level: Level;

	public constructor(
		private readonly game: TraderGame,
		private readonly state: State<HeroState>
	) {
		this.health = new Health(
			this,
			state.subState('healthDeficit')
		);

		this.level = new Level(
			state.subState('xp')
		);
	}


	public get activity() {
		return this.state.current.activity;
	}

	private get entry() {
		return Hero.registry.get(this.key);
	}

	public get equipment() {
		return this.state.current.equipment;
	}

	public get icon() {
		return this.entry.icon;
	}

	public get key() {
		return this.state.current.key;
	}

	public get rotation() {
		return this.state.current.rotation;
	}

	public set rotation(rotation: AttackRotation) {
		this.state.patch({ rotation });
	}


	public getAttribute(attribute: Attribute) {
		let equipmentBonus = 0;
		for (const slot of equipmentSlotKeys) {
			const itemKey = this.equipment[slot];
			if (itemKey) {
				const item = enumGet(ItemKinds, itemKey);
				equipmentBonus += item.attributes?.[attribute] ?? 0;
			}
		}

		return (
			(this.entry.baseAttributes[attribute] ?? 0) +
			(this.level.value - 1) * (this.entry.levelAdvancement[attribute] ?? 0) +
			equipmentBonus
		);
	}

	public scheduleQuest(questKey: string) {
		if (this.activity.kind !== HeroActivity.IDLE) {
			throw new Error('Cannot schedule a quest when the hero is not IDLE.');
		}

		const quest = questRegistry.get(questKey);
		if (quest.levelRequirement > this.level.value) {
			throw new Error('Hero must meet the level requirement of a quest.');
		}

		const levelDiff = quest.levelRequirement - this.level.value;
		const durationMs = quest.baseDurationMs * (
			levelDiff < 0
				? 0.75 / (1 - levelDiff / 3) + 0.25
				: 1 + Math.sqrt(levelDiff)
		);

		this.state.patch({
			activity: {
				kind: HeroActivity.QUEST_PENDING,
				durationMs,
				elapsesAt: this.game.clock.now + durationMs,
				questKey
			}
		});
	}

	public completeQuest() {
		if (this.activity.kind !== HeroActivity.QUEST_PENDING || this.activity.elapsesAt > this.game.clock.now) {
			throw new Error('Cannot complete a quest when it did not elapse yet.');
		}

		const quest = questRegistry.get(this.activity.questKey);
		const simulator = ScopedCombatSimulator.create([
			{
				entity: this,
				side: CombatSide.GOOD
			},
			{
				entity: new Enemy(quest.enemyKey),
				side: CombatSide.EVIL
			}
		]);

		// TODO: combat events do not serialize correctly yet
		const combatEvents: readonly CombatEvent[] = [];
		simulator.run();

		if (this.health.isAlive) {
			const levelDiff = quest.levelRequirement - this.level.value;
			const xp = this.level.xpRequiredForNextLevel * quest.xpModifier * (
				levelDiff < 0
					? 1 / (1 - levelDiff)
					: 1 + (levelDiff / 2) ** 2
			);

			this.state.patch({
				activity: {
					kind: HeroActivity.QUEST_READY,
					loot: LootTable.generateLoot(quest.lootTable),
					combatEvents,
					xp
				}
			});
		}
		else {
			this.state.patch({
				activity: {
					combatEvents,
					kind: HeroActivity.DEAD
				}
			});
		}
	}

	public deliverQuest() {
		if (this.activity.kind !== HeroActivity.QUEST_READY) {
			throw new Error('Cannot deliver quest loot when not ready.');
		}

		this.game.inventory.add(this.activity.loot);
		this.level.xp += this.activity.xp;
		this.state.patch({
			activity: {
				kind: HeroActivity.IDLE
			}
		});
	}

	public revive() {
		if (this.activity.kind !== HeroActivity.DEAD) {
			throw new Error('Cannot revive hero when not dead.');
		}

		this.health.reset();
		this.state.patch({
			activity: {
				kind: HeroActivity.IDLE
			}
		});
	}

	public equip(newItemKey: string) {
		const newItem = enumGet(ItemKinds, newItemKey);
		if (newItem.slot === undefined) {
			throw new Error('This item does not fit into any slots.');
		}

		this.game.inventory.remove([
			{
				itemKey: newItemKey,
				quantity: 1
			}
		]);

		const oldItemKey = this.equipment[newItem.slot];
		if (oldItemKey) {
			// since all equipment is non-stackable, at least one slot is surely free
			this.game.inventory.add([
				{
					itemKey: oldItemKey,
					quantity: 1
				}
			]);
		}

		this.state.patch({
			equipment: {
				...this.equipment,
				[newItem.slot]: newItemKey
			}
		});
	}


	public static readonly registry = new Registry<HeroInfo>();

	public static create(key: string): HeroState {
		const entry = Hero.registry.get(key);
		return {
			key,
			equipment: entry.startEquipment,
			healthDeficit: 0,
			rotation: entry.rotation,
			xp: 0,
			activity: {
				kind: HeroActivity.IDLE
			}
		};
	}
}
