import { useIntl } from '@calmdownval/intl-preact';
import { h } from 'preact';

import { useRouter } from '~/components/AppRouter';
import { EntryCard } from '~/components/EntryCard';
import { FlatLayout } from '~/components/FlatLayout';
import { Menu } from '~/components/Menu';
import { MainHeader } from '~/components/Typography';
import { Enemy } from '~/game/entities/Enemy';
import type { Hero } from '~/game/entities/Hero';
import { questRegistry } from '~/game/quests/Quest';
import * as Route from '~/routes';
import type { ScreenType } from '~/screens/ScreenType';

interface QuestSelectScreenParams {
	readonly hero: Hero;
}

export const QuestSelectScreen: ScreenType = () => {
	const { t } = useIntl();
	const router = useRouter<QuestSelectScreenParams>();
	const { hero } = router.params;
	return (
		<FlatLayout>
			<Menu
				header={
					<MainHeader>
						{t('ui.title.quest-select')}
					</MainHeader>
				}
				items={questRegistry.keys}
				itemRenderer={questKey => {
					const quest = questRegistry.get(questKey);
					const enemy = Enemy.registry.get(quest.enemyKey);
					return (
						<EntryCard
							icon={enemy.icon}
							quantity={quest.levelRequirement}
							title={t(`quest.${quest.key}.name`)}
							actionDisabled={quest.levelRequirement > hero.level.value}
							action={() => router.navigate(Route.QUEST_START, {
								hero,
								quest
							})}
						>
							<p class='quest-select__description'>
								{t(`quest.${quest.key}.desc`)}
							</p>
						</EntryCard>
					);
				}}
			/>
		</FlatLayout>
	);
};
