import { useIntl } from '@calmdownval/intl-preact';
import { h } from 'preact';
import { useState } from 'preact/hooks';

import { useRouter } from '~/components/AppRouter';
import { Button } from '~/components/Button';
import { EntryCard } from '~/components/EntryCard';
import { Menu } from '~/components/Menu';
import { StickyLayout } from '~/components/StickyLayout';
import { MainHeader } from '~/components/Typography';
import { Hero } from '~/game/entities/Hero';
import * as Route from '~/routes';
import type { ScreenType } from '~/screens/ScreenType';

export const HeroRecruitScreen: ScreenType = ({ game }) => {
	const { t } = useIntl();
	const router = useRouter();
	const [ selectedKey, setSelectedKey ] = useState<string | null>(null);
	const selectedHero = selectedKey ? Hero.registry.get(selectedKey) : null;
	const header = (
		<header class='recruit-screen__header'>
			<MainHeader>
				{t('ui.title.recruit-hero')}
			</MainHeader>
			<EntryCard
				icon={selectedHero?.icon}
				title={selectedHero ? t(`entity.${selectedHero.key}`) : undefined}
				actionDisabled={!selectedHero}
				action={() => {
					game.recruitHero(selectedKey!);
					router.navigate(Route.HEROES);
				}}
			/>
		</header>
	);

	return (
		<StickyLayout header={header}>
			<Menu
				items={Hero.registry.keys}
				itemRenderer={key => (
					<Button
						key={key}
						selected={key === selectedKey}
						onClick={() => setSelectedKey(key)}
						children={t(`entity.${key}`)}
					/>
				)}
			/>
		</StickyLayout>
	);
};
