import { h } from 'preact';

import { EntryCard } from '~/components/EntryCard';
import { SubHeader } from '~/components/Typography';
import { HeroQuestReadyActivity } from '~/game/entities/Hero';
import * as Route from '~/routes';

import type { HeroEntryRenderer } from './types';

export const HeroQuestReadyEntryRenderer: HeroEntryRenderer<HeroQuestReadyActivity> = (_activity, {
	hero,
	router,
	t,
	...rest
}) => (
	<EntryCard
		{...rest}
		action={() => router.navigate(Route.QUEST_END, { hero })}
	>
		<SubHeader>
			{t('ui.hero-state.quest-ready')}
		</SubHeader>
	</EntryCard>
);
