import { IntlProvider } from '@calmdownval/intl-preact';
import { h } from 'preact';
import { useMemo } from 'preact/hooks';

import { AppRouter } from '~/components/AppRouter';
import { LocalStorageBackend } from '~/game/storage/LocalStorageBackend';
import { StateStorage } from '~/game/storage/StateStorage';
import { TraderGame } from '~/game/TraderGame';
import * as Route from '~/routes';
import { CraftersScreen } from '~/screens/crafters';
import { CraftingEndScreen } from '~/screens/crafting-end';
import { CraftingListScreen } from '~/screens/crafting-list';
import { CraftingRecipeScreen } from '~/screens/crafting-recipe';
import { HeroDeathScreen } from '~/screens/hero-death';
import { HeroEditScreen } from '~/screens/hero-edit';
import { HeroEquipItem } from '~/screens/hero-equip-item';
import { HeroHealScreen } from '~/screens/hero-heal';
import { HeroRecruitScreen } from '~/screens/hero-recruit';
import { HeroesScreen } from '~/screens/heroes';
import { InventoryScreen } from '~/screens/inventory';
import { QuestEndScreen } from '~/screens/quest-end';
import { QuestSelectScreen } from '~/screens/quest-select';
import { QuestStartScreen } from '~/screens/quest-start';
import { ScreenType } from '~/screens/ScreenType';
import { ShopScreen } from '~/screens/shop';

export const App = () => {
	const routes = useMemo(initializeGameAndRoutes, []);
	return (
		<IntlProvider url='./locale/en.json'>
			<AppRouter
				initialPath={Route.CRAFTERS}
				routes={routes}
			/>
		</IntlProvider>
	);
};

function initializeGameAndRoutes() {
	const state = new StateStorage(
		new LocalStorageBackend({
			key: 'game-state'
		})
	);

	const game = new TraderGame(state);
	const bind = (screen: ScreenType) => () => screen({ game });

	// register debug utils
	(window as any).tickFiveMins = () => {
		game.clock.now += 5 * 60 * 1000;
	};

	return [
		{
			path: Route.CRAFTERS,
			content: bind(CraftersScreen),
			visible: true
		},
		{
			path: Route.CRAFTING_LIST,
			content: bind(CraftingListScreen)
		},
		{
			path: Route.CRAFTING_RECIPE,
			content: bind(CraftingRecipeScreen)
		},
		{
			path: Route.CRAFTING_END,
			content: bind(CraftingEndScreen)
		},
		{
			path: Route.INVENTORY,
			content: bind(InventoryScreen),
			visible: true
		},
		{
			path: Route.SHOP,
			content: bind(ShopScreen),
			visible: true
		},
		{
			path: Route.HEROES,
			content: bind(HeroesScreen),
			visible: true
		},
		{
			path: Route.HERO_RECRUIT,
			content: bind(HeroRecruitScreen)
		},
		{
			path: Route.HERO_EDIT,
			content: bind(HeroEditScreen)
		},
		{
			path: Route.HERO_EQUIP_ITEM,
			content: bind(HeroEquipItem)
		},
		{
			path: Route.HERO_DEATH,
			content: bind(HeroDeathScreen)
		},
		{
			path: Route.HERO_HEAL_SCREEN,
			content: bind(HeroHealScreen)
		},
		{
			path: Route.QUEST_SELECT,
			content: bind(QuestSelectScreen)
		},
		{
			path: Route.QUEST_START,
			content: bind(QuestStartScreen)
		},
		{
			path: Route.QUEST_END,
			content: bind(QuestEndScreen)
		}
	];
}
