import { useIntl } from '@calmdownval/intl-preact';
import { Fragment, h } from 'preact';
import { useState } from 'preact/hooks';

import { EntryCard } from '~/components/EntryCard';
import { ItemGrid } from '~/components/ItemGrid';
import { StickyLayout } from '~/components/StickyLayout';
import { MainHeader } from '~/components/Typography';
import { ItemKinds } from '~/game/items/ItemKind';
import { ItemStack } from '~/game/items/ItemStack';
import type { ScreenType } from '~/screens/ScreenType';
import { enumGet } from '~/utils/enum';

export const InventoryScreen: ScreenType = ({ game }) => {
	const { t } = useIntl();
	const [ selectedIndex, setSelectedIndex ] = useState(-1);

	const { capacity, contents } = game.inventory;
	const slots = new Array<ItemStack | null>(capacity);
	for (let i = 0; i < capacity; ++i) {
		slots[i] = i < contents.length ? contents[i] : null;
	}

	const selectedStack = slots[selectedIndex];
	const header = (
		<>
			<MainHeader>
				{t('ui.title.inventory')}
			</MainHeader>
			<EntryCard
				class='item-grid__detail'
				icon={selectedStack ? enumGet(ItemKinds, selectedStack.itemKey).icon : undefined}
				quantity={selectedStack?.quantity}
				title={selectedStack ? t(`item.${selectedStack.itemKey}`) : undefined}
			/>
		</>
	);

	return (
		<StickyLayout header={header}>
			<ItemGrid
				content={slots}
				onClickItem={(_, index) => setSelectedIndex(index)}
			/>
		</StickyLayout>
	);
};
