export interface EnumType {
	readonly key: string;
}

export type EnumDefinition<TItem extends EnumType> = {
	readonly [K in string]?: TItem;
};

type Mutable<T> = { -readonly [K in keyof T]: T[K] };

export function enumOf<T extends EnumType>() {
	return <E extends Record<string, Omit<T, 'key'>>>(obj: E) => {
		Object.keys(obj).forEach(key => {
			(obj[key] as Mutable<T>).key ??= key;
		});

		return obj as unknown as { [K in keyof E]: T };
	};
}

export function enumGet<T extends EnumType>(enumObj: EnumDefinition<T>, key: string) {
	const member = enumObj[key];
	if (typeof member !== 'object' || member.key !== key) {
		throw new Error(`No enum member with the key '${key}' found.`);
	}

	return member;
}

export function enumMap<T extends EnumType>(enumObj: EnumDefinition<T>, callback: (value: T, key: string) => T): T[] {
	const result: T[] = [];
	for (const key in enumObj) {
		const member = enumObj[key];
		if (typeof member === 'object' && member.key === key) {
			result.push(callback(member, key));
		}
	}

	return result;
}

// this function maps a regular type script enum rather than our java-like enums
export function tsEnumMap<T>(
	enumObj: Record<string | number, number | string>,
	callback: (value: number, key: string) => T
): T[] {
	const result: T[] = [];
	for (const key in enumObj) {
		const value = enumObj[key];
		if (typeof value === 'string' && Object.prototype.hasOwnProperty.call(enumObj, value)) {
			continue;
		}

		result.push(callback(value as number, key));
	}

	return result;
}
