import * as Signal from '@calmdownval/signal';

import { first } from '~/utils/array';
import { clamp } from '~/utils/math';
import { binarySearch } from '~/utils/order';

import type { Clock } from './Clock';

export interface TimerCallback {
	(now: number): void;
}

interface Timer {
	readonly callback: TimerCallback;
	readonly handle: number;
	readonly interval?: number;
	readonly timestamp: number;
}

function compareTimers(a: Timer, b: Timer) {
	return a.timestamp - b.timestamp;
}

export class Scheduler {
	private handle?: number;
	private handleCounter = 0;
	private queue: Timer[] = [];

	public constructor(
		public readonly clock: Clock,
		private readonly maxTimeoutMs = 30_000
	) {
		Signal.on(clock.changed, this.scheduleNextTick);
	}

	public dispose() {
		Signal.off(this.clock.changed, this.scheduleNextTick);
	}

	public setTimeout(callback: TimerCallback, delay: number) {
		return this.insertAndSchedule(callback, delay, false);
	}

	public setInterval(callback: TimerCallback, delay: number) {
		return this.insertAndSchedule(callback, delay, true);
	}

	public scheduleCallbackAt(callback: TimerCallback, clockTimestamps: readonly number[]) {
		const handle = ++this.handleCounter;
		for (let i = 0; i < clockTimestamps.length; ++i) {
			this.insert({
				callback,
				handle,
				timestamp: clockTimestamps[i]
			});
		}

		return handle;
	}

	public clear(handle: number) {
		this.queue = this.queue.filter(timer => timer.handle !== handle);
		this.scheduleNextTick();
	}

	private insertAndSchedule(callback: TimerCallback, delay: number, isRecurring: boolean) {
		const timer: Timer = {
			callback,
			handle: ++this.handleCounter,
			interval: isRecurring ? delay : undefined,
			timestamp: this.clock.now + delay,
		};

		this.insert(timer);
		this.scheduleNextTick();
		return timer.handle;
	}

	private insert(timer: Timer) {
		let index = binarySearch(this.queue, timer, compareTimers);
		if (index < 0) {
			index = -index - 1;
		}
		else {
			do {
				++index;
			}
			while (index < this.queue.length && compareTimers(this.queue[index], timer) === 0);
		}

		this.queue.splice(index, 0, timer);
	}

	private readonly scheduleNextTick = () => {
		const timestamp = first(this.queue)?.timestamp;
		if (timestamp === undefined) {
			return;
		}

		this.cancelTick();

		const delayMs = clamp(timestamp - this.clock.now, 0, this.maxTimeoutMs);
		this.handle = setTimeout(this.tick, delayMs);
	};

	private cancelTick() {
		if (this.handle !== undefined) {
			clearTimeout(this.handle);
			this.handle = undefined;
		}
	}

	private readonly tick = () => {
		const { now } = this.clock;
		let count = 0;
		while (count < this.queue.length) {
			if (this.queue[count].timestamp > now) {
				break;
			}

			++count;
		}

		const elapsed = this.queue.splice(0, count);
		try {
			for (let i = 0; i < count; ++i) {
				const timer = elapsed[i];
				timer.callback(now);

				if (timer.interval) {
					this.insert({
						...timer,
						timestamp: now + timer.interval
					});
				}
			}
		}
		finally {
			this.handle = undefined;
			this.scheduleNextTick();
		}
	};
}
