export interface RegistryEntry {
	readonly key: string;
}

export class Registry<T extends RegistryEntry> {
	private readonly entries: { [K in string]?: T } = {};

	public get keys(): readonly string[] {
		return Object.keys(this.entries);
	}

	public add(entry: T) {
		if (this.entries[entry.key]) {
			throw new Error(`Entry with the key '${entry.key}' has already been registered.`);
		}

		this.entries[entry.key] = entry;
		return entry;
	}

	public get(key: string) {
		const entry = this.entries[key];
		if (!entry) {
			throw new Error(`Entry with the key '${key}' has not been registered.`);
		}

		return entry;
	}
}
