import { useIntl } from '@calmdownval/intl-preact';
import { h } from 'preact';

import { useRouter } from '~/components/AppRouter';
import { EntryCard } from '~/components/EntryCard';
import { FlatLayout } from '~/components/FlatLayout';
import { Menu } from '~/components/Menu';
import { ProgressBar } from '~/components/ProgressBar';
import { MainHeader } from '~/components/Typography';
import { Hero } from '~/game/entities/Hero';
import { equipmentSlotKeys } from '~/game/items/EquipmentSlot';
import { ItemKinds } from '~/game/items/ItemKind';
import * as Route from '~/routes';
import { enumGet } from '~/utils/enum';

import { ScreenType } from '../ScreenType';


interface HeroEditScreenParams {
	readonly hero: Hero;
}

export const HeroEditScreen: ScreenType = () => {
	const { t } = useIntl();
	const router = useRouter<HeroEditScreenParams>();
	const { hero } = router.params;

	const header = (
		<header class='edit-hero-screen__header'>
			<MainHeader>
				{t('ui.title.equipment')}
			</MainHeader>
		</header>
	);

	return (
		<FlatLayout>
			<EntryCard
				icon={hero.icon}
				quantity={hero.level.value}
				action={() => {
					router.navigate(Route.HERO_HEAL_SCREEN, { hero });
				}}
			>
				{t(`entity.${hero.key}`)}
				<ProgressBar
					title={t('ui.label.hp')}
					value={hero.health.value}
					maxValue={hero.health.maxHealth}
				/>
				<ProgressBar
					title={t('ui.label.xp')}
					value={hero.level.xpThisLevel}
					maxValue={hero.level.xpRequiredForNextLevel}
				/>
			</EntryCard>
			<Menu
				header={header}
				items={equipmentSlotKeys}
				itemRenderer={slot => {
					const itemKey = hero.equipment[slot];
					return (
						<EntryCard
							icon={itemKey ? enumGet(ItemKinds, itemKey).icon : undefined}
							title={itemKey && t(`item.${itemKey}`)}
							action={() => {
								router.navigate(Route.HERO_EQUIP_ITEM, {
									hero,
									slot
								});
							}}
						/>
					);
				}}
			/>
		</FlatLayout>
	);
};
