import { h } from 'preact';

import { Icon } from '~/components/Icon';
import { ItemKinds } from '~/game/items/ItemKind';
import { ItemStack } from '~/game/items/ItemStack';
import { enumGet } from '~/utils/enum';
import { cx } from '~/utils/style';

import './ItemGrid.css';

export interface ItemGridProps {
	readonly class?: string;
	readonly content: readonly (ItemStack | null)[];
	readonly onClickItem?: (stack: ItemStack | null, index: number) => void;
}

export const ItemGrid = ({
	class: customClass,
	content,
	onClickItem
}: ItemGridProps) => (
	<div class={cx('item-grid', customClass)}>
		{content.map((itemStack, index) => (
			<Icon
				key={index}
				quantity={itemStack?.quantity}
				icon={itemStack ? enumGet(ItemKinds, itemStack.itemKey).icon : undefined}
				onClick={() => onClickItem?.(itemStack, index)}
			/>
		))}
	</div>
);
