import { useIntl } from '@calmdownval/intl-preact';
import { h } from 'preact';

import { useRouter } from '~/components/AppRouter';
import { Button } from '~/components/Button';
import { EntityDetail } from '~/components/EntityDetail';
import { FlatLayout } from '~/components/FlatLayout';
import { MainHeader } from '~/components/Typography';
import { Enemy } from '~/game/entities/Enemy';
import type { Hero } from '~/game/entities/Hero';
import type { Quest } from '~/game/quests/Quest';
import * as Route from '~/routes';
import type { ScreenType } from '~/screens/ScreenType';

import './QuestStartScreen.css';

interface QuestStartScreenParams {
	readonly hero: Hero;
	readonly quest: Quest;
}

export const QuestStartScreen: ScreenType = () => {
	const { t } = useIntl();
	const router = useRouter<QuestStartScreenParams>();
	const { hero, quest } = router.params;
	return (
		<FlatLayout class='quest-start'>
			<MainHeader class='quest-start__header'>
				{t('ui.title.quest-start')}
			</MainHeader>
			<EntityDetail
				class='quest-start__hero-detail'
				entity={hero}
			/>
			<div class='quest-start__quest-prompt'>
				<span class='quest-start__versus'>
					{t('ui.label.versus')}
				</span>
				<Button
					class='quest-start__start-button'
					onClick={() => {
						hero.scheduleQuest(quest.key);
						router.navigate(Route.HEROES);
					}}
				>
					{t('ui.label.start-quest')}
				</Button>
			</div>
			<EntityDetail
				class='quest-start__enemy-detail'
				entity={new Enemy(quest.enemyKey)}
			/>
		</FlatLayout>
	);
};
