import { h } from 'preact';

import { Countdown } from '~/components/Countdown';
import { EntryCard } from '~/components/EntryCard';
import { SubHeader } from '~/components/Typography';
import { HeroQuestPendingActivity } from '~/game/entities/Hero';
import * as Route from '~/routes';

import type { HeroEntryRenderer } from './types';

export const HeroQuestPendingEntryRenderer: HeroEntryRenderer<HeroQuestPendingActivity> = (activity, {
	game,
	hero,
	router,
	t,
	...rest
}) => {
	const remainingTimeMs = activity.elapsesAt - game.clock.now;
	return (
		<EntryCard
			{...rest}
			action={() => {
				hero.completeQuest();
				if (hero.health.isAlive) {
					router.navigate(Route.QUEST_END, { hero });
				}
				else {
					router.navigate(Route.HERO_DEATH, { hero });
				}
			}}
			actionDisabled={remainingTimeMs > 0}
			actionContent={remainingTimeMs > 0
				? (
					<Countdown
						scheduler={game.scheduler}
						timestamp={activity.elapsesAt}
					/>
				)
				: undefined
			}
		>
			<SubHeader>
				{t(`ui.hero-state.${remainingTimeMs > 0 ? 'quest-pending' : 'quest-ready'}`)}
			</SubHeader>
		</EntryCard>
	);
};
