import { useEffect } from 'preact/hooks';

import { Scheduler } from '~/game/time/Scheduler';

import { useForceUpdate } from './useForceUpdate';

export function usePeriodicUpdates(scheduler: Scheduler, interval?: number) {
	const forceUpdate = useForceUpdate();
	useEffect(() => {
		if (interval === undefined || interval < 1) {
			return undefined;
		}

		const handle = scheduler.setInterval(forceUpdate, interval);
		return () => scheduler.clear(handle);
	}, [ interval ]);
}
