import { h, RenderableProps } from 'preact';

import { bem, cx } from '~/utils/style';

import './Typography.css';

interface TypographyProps {
	readonly class?: string;
}

export const MainHeader = ({ class: customClass, children }: RenderableProps<TypographyProps>) => (
	<h2 class={cx(bem('typography-header', { main: true }), customClass)}>
		{children}
	</h2>
);

export const SubHeader = ({ class: customClass, children }: RenderableProps<TypographyProps>) => (
	<h3 class={cx(bem('typography-header', { sub: true }), customClass)}>
		{children}
	</h3>
);
