import { useIntl } from '@calmdownval/intl-preact';
import { ComponentType, createContext, Fragment, h } from 'preact';
import { useContext, useState } from 'preact/hooks';

import { Button } from '~/components/Button';
import { Menu, Orientation } from '~/components/Menu';

import './AppRouter.css';

export interface RouteInfo {
	readonly path: string;
	readonly content: ComponentType;
	readonly visible?: boolean;
}

export interface RouterContextType<TParams> {
	readonly route: RouteInfo;
	readonly params: TParams;
	navigate(path: string, params?: any): void;
}

const RouterContext = createContext<RouterContextType<any> | null>(null);

export function useRouter<TParams = any>(): RouterContextType<TParams> {
	return useContext(RouterContext)!;
}

interface CurrentRoute {
	readonly route: RouteInfo;
	readonly params: any;
}

export interface AppRouterProps {
	readonly initialPath?: string;
	readonly routes: readonly RouteInfo[];
}

export const AppRouter = ({ initialPath, routes }: AppRouterProps) => {
	const { t } = useIntl();
	const [ state, setState ] = useState<CurrentRoute>(() => {
		const route = initialPath === undefined
			? undefined
			: routes.find(r => r.path === initialPath);

		return {
			route: route ?? routes[0],
			params: null
		};
	});

	const navigate = (path: string, params?: any) => {
		const route = routes.find(r => r.path === path);
		if (!route) {
			throw new Error(`No route with path '${path}' exists.`);
		}

		setState({
			route,
			params
		});
	};

	return (
		<>
			<div class='app-router__content'>
				<div class='app-router__container'>
					<RouterContext.Provider
						value={{
							...state,
							navigate
						}}
					>
						{h(state.route.content, {})}
					</RouterContext.Provider>
				</div>
			</div>
			<div class='app-router__menu'>
				<Menu
					class='app-router__container'
					orientation={Orientation.Horizontal}
					items={routes.filter(route => route.visible === true)}
					itemRenderer={({ path }) => (
						<Button
							key={path}
							selected={path === state.route.path}
							children={t(`ui.route.${path}`)}
							onClick={() => navigate(path)}
						/>
					)}
					uniform
				/>
			</div>
		</>
	);
};
