import { enumGet } from '~/utils/enum';

import { Attacks } from './Attack';
import { Combatant, CombatSide } from './Combatant';
import { CombatEvent, CombatEventKind } from './CombatEvent';
import { CombatParticipant } from './CombatParticipant';
import { AppliedEffect, Effects } from './Effect';
import type { LivingEntity } from './LivingEntity';
import { CombatParticipants, CombatSimulator, OngoingCombatSimulator } from './types';

export interface CombatParticipantInit {
	readonly entity: LivingEntity;
	readonly side: CombatSide;
}

export class ScopedCombatSimulator implements OngoingCombatSimulator, CombatSimulator {
	public readonly participants: CombatParticipants;

	private readonly events: CombatEvent[] = [];
	private readonly allParticipants: readonly CombatParticipant[];
	private _currentAttacker!: Combatant;

	private constructor(participantsInit: readonly CombatParticipantInit[]) {
		this.allParticipants = participantsInit.map(init => new CombatParticipant(this, init.entity, init.side));

		const evil = [];
		const good = [];
		for (const participant of this.allParticipants) {
			switch (participant.side) {
				case CombatSide.GOOD:
					good.push(participant);
					break;

				case CombatSide.EVIL:
					evil.push(participant);
					break;
			}
		}

		this.participants = {
			evil,
			good
		};
	}


	public get currentAttacker() {
		return this._currentAttacker;
	}


	public run() {
		// run the simulation until members of one side are all dead
		let round = 0;
		let goodGuysAlive;
		do {
			this.report({
				kind: CombatEventKind.ROUND_START,
				round: (round + 1)
			});

			for (const participant of this.allParticipants) {
				if (!participant.isAlive) {
					continue;
				}

				const { rotation } = participant.entity;
				const attack = rotation[round % rotation.length];

				this.runAttack(participant, attack);
				participant.tickEffects();
			}

			if (++round === 100) {
				break;
			}
		}
		while ((goodGuysAlive = this.participants.good.some(isAlive)) && this.participants.evil.some(isAlive));

		// tick effects for good guys
		if (goodGuysAlive) {
			let pendingEffects = this.participants.good.some(hasEffects);
			while (pendingEffects) {
				this.report({
					kind: CombatEventKind.ROUND_START,
					round: (round + 1)
				});

				pendingEffects = false;
				for (const participant of this.participants.good) {
					if (hasEffects(participant)) {
						participant.tickEffects();
						pendingEffects = true;
					}
				}

				if (++round === 150) {
					break;
				}
			}
		}

		return this.events;
	}

	public runAttack(attacker: Combatant, attackKey: string) {
		const attack = enumGet(Attacks, attackKey);
		this._currentAttacker = attacker;
		attack.execute(attacker, this.participants);
	}

	public runEffect(combatant: Combatant, applied: AppliedEffect) {
		const effect = enumGet(Effects, applied.effectKey);
		this._currentAttacker = applied.source;
		effect.tick.call(applied, combatant);
	}

	public report(event: CombatEvent) {
		this.events.push(event);
	}


	public static create(participantsInit: readonly CombatParticipantInit[]): CombatSimulator {
		return new ScopedCombatSimulator(participantsInit);
	}
}

function isAlive(participant: Combatant) {
	return participant.isAlive;
}

function hasEffects(participant: Combatant) {
	return participant.isAlive && participant.effects.length > 0;
}
