/**
 * Represents a comparison function typically used for sorting. The return value
 * indicates the order:
 *
 * - Values <0 indicate A precedes B
 * - Values >0 indicate B precedes A
 * - Value of 0 indicates A = B
 */
export interface Compare<T> {
	(a: T, b: T): number;
}

/**
 * Searches for an item within a sorted array using the binary search algorithm.
 * The array is assumed to be sorted in ascending order according to the compare
 * function, otherwise the behavior of this function is undefined.
 *
 * If an equal item is found, its index is returned. Otherwise the insertion
 * index is returned as `-index - 1`.
 *
 * If multiple equal items are present, there is no guarantee which index will
 * be returned.
 * @param array The sorted array of items
 * @param compare The compare function
 */
export function binarySearch<T>(sortedArray: readonly T[], item: T, compare: Compare<T>) {
	let min = 0;
	let max = sortedArray.length;
	let mid;
	let test;

	while (min < max) {
		mid = (min + max) >>> 1;
		test = compare(item, sortedArray[mid]);

		if (test > 0) {
			min = mid + 1;
		}
		else if (test < 0) {
			max = mid;
		}
		else {
			return mid;
		}
	}

	return -min - 1;
}
