import { h } from 'preact';

import { cx } from '~/utils/style';

import './Icon.css';

export interface IconProps {
	readonly class?: string;
	readonly icon?: string;
	readonly onClick?: () => void;
	readonly quantity?: number;
}

export const Icon = ({
	class: customClass,
	icon,
	onClick,
	quantity
}: IconProps) => (
	<div
		class={cx('icon', customClass)}
		onClick={onClick}
	>
		<svg viewBox='0 0 100 100'>
			<text
				style='font-size: 80'
				alignment-baseline='middle'
				text-anchor='middle'
				x={50}
				y={60}
			>
				{icon}
			</text>
		</svg>
		{icon && quantity && (
			<span class='icon__quantity'>
				{quantity}
			</span>
		)}
	</div>
);
