import { ComponentChildren, h, RenderableProps } from 'preact';

import { cx } from '~/utils/style';

import './StickyLayout.css';

export interface StickyLayoutProps {
	readonly class?: string;
	readonly header?: ComponentChildren;
}

export const StickyLayout = ({
	class: customClass,
	children,
	header
}: RenderableProps<StickyLayoutProps>) => (
	<div class={cx('sticky-layout', customClass)}>
		{header && (
			<header class='sticky-layout__header'>
				{header}
			</header>
		)}
		{children}
	</div>
);
