import { h } from 'preact';

import { EntryCard } from '~/components/EntryCard';
import { ProgressBar } from '~/components/ProgressBar';
import type { CrafterIdleActivity } from '~/game/entities/Crafter';
import * as Route from '~/routes';

import type { CrafterEntryRenderer } from './types';

export const CrafterIdleEntryRenderer: CrafterEntryRenderer<CrafterIdleActivity> = (_activity, {
	crafter,
	router,
	t,
	...rest
}) => (
	<EntryCard
		{...rest}
		action={() => router.navigate(Route.CRAFTING_LIST, { crafter })}
	>
		<ProgressBar
			value={crafter.level.xpThisLevel}
			maxValue={crafter.level.xpRequiredForNextLevel}
		/>
	</EntryCard>
);
