import { ComponentChildren, h, RenderableProps } from 'preact';

import { Button } from '~/components/Button';
import { Icon } from '~/components/Icon';
import { SubHeader } from '~/components/Typography';
import { bem, cx } from '~/utils/style';

import './EntryCard.css';

export interface EntryCardProps {
	readonly action?: () => void;
	readonly actionContent?: ComponentChildren;
	readonly actionDisabled?: boolean;
	readonly class?: string;
	readonly icon?: string;
	readonly quantity?: number;
	readonly secondaryAction?: () => void;
	readonly title?: string;
}

export const EntryCard = ({
	action,
	actionContent = '',
	actionDisabled = false,
	children,
	class: customClass,
	icon,
	quantity,
	secondaryAction,
	title
}: RenderableProps<EntryCardProps>) => (
	<div class={cx(bem('entry-card', { 'has-action': Boolean(action) }), customClass)}>
		<Icon
			class='entry-card__icon'
			icon={icon}
			quantity={quantity}
			onClick={secondaryAction}
		/>
		<div class='entry-card__meta'>
			{title && (
				<SubHeader>
					{title}
				</SubHeader>
			)}
			{children}
		</div>
		{action && (
			<Button
				class='entry-card__action'
				disabled={actionDisabled}
				onClick={() => action()}
			>
				{actionContent}
			</Button>
		)}
	</div>
);
