import type { Effect } from '~/game/entities/combat/Effect';

import type { Combatant } from './Combatant';

export enum CombatEventKind {
	DIRECT_DAMAGE,
	DIRECT_HEAL,
	EFFECT_APPLIED,
	EFFECT_STACKED,
	EFFECT_FADED,
	EFFECT_REMOVED,
	PARTICIPANT_DIED,
	PARTICIPANT_REVIVED,
	ROUND_START
}

interface CombatEventBase<TKind extends CombatEventKind> {
	readonly kind: TKind;
}

export interface DirectDamageOrHealEvent extends CombatEventBase<CombatEventKind.DIRECT_DAMAGE | CombatEventKind.DIRECT_HEAL> {
	readonly source: Combatant;
	readonly target: Combatant;
	readonly healthChange: number;
}

export interface EffectAppliedEvent extends CombatEventBase<CombatEventKind.EFFECT_APPLIED> {
	readonly source: Combatant;
	readonly target: Combatant;
	readonly effectKey: string;
}

export interface EffectStackedEvent extends CombatEventBase<CombatEventKind.EFFECT_STACKED> {
	readonly source: Combatant;
	readonly target: Combatant;
	readonly effectKey: string;
	readonly stackCount: number;
}

export interface EffectFadedOrRemovedEvent extends CombatEventBase<CombatEventKind.EFFECT_FADED | CombatEventKind.EFFECT_REMOVED> {
	readonly target: Combatant;
	readonly effectKey: string;
}

export interface ParticipantDiedEvent extends CombatEventBase<CombatEventKind.PARTICIPANT_DIED> {
	readonly target: Combatant;
}

export interface ParticipantRevivedEvent extends CombatEventBase<CombatEventKind.PARTICIPANT_REVIVED> {
	readonly target: Combatant;
	readonly revivalHealth: number;
}

export interface RoundStartEvent extends CombatEventBase<CombatEventKind.ROUND_START> {
	readonly round: number;
}

export type CombatEvent =
	| DirectDamageOrHealEvent
	| EffectAppliedEvent
	| EffectStackedEvent
	| EffectFadedOrRemovedEvent
	| ParticipantDiedEvent
	| ParticipantRevivedEvent
	| RoundStartEvent;
