import { MAX_HEROES } from '~/game/constants';
import { Crafter, CrafterState } from '~/game/entities/Crafter';
import { Hero, HeroState } from '~/game/entities/Hero';
import { Inventory, InventoryState } from '~/game/items/Inventory';
import { PlayerError, PlayerErrorKind } from '~/game/PlayerError';
import { StateStorage } from '~/game/storage/StateStorage';
import { Clock } from '~/game/time/Clock';
import { Scheduler } from '~/game/time/Scheduler';

import '~/game/entities/crafters';
import '~/game/entities/enemies';
import '~/game/entities/heroes';
import '~/game/quests';

export interface TraderGameState {
	readonly crafters: CrafterState[];
	readonly heroes: HeroState[];
	readonly inventory: InventoryState;
}

export class TraderGame {
	public readonly clock: Clock;
	public readonly scheduler: Scheduler;
	public readonly inventory: Inventory;

	private readonly _crafters: Crafter[];
	private readonly _heroes: Hero[];

	public constructor(
		private readonly state: StateStorage<TraderGameState>
	) {
		this.clock = new Clock();
		this.scheduler = new Scheduler(this.clock);

		state.init({
			crafters: [
				Crafter.create('BLACKSMITH'),
				Crafter.create('CARVER'),
				Crafter.create('HERBALIST')
			],
			heroes: [ Hero.create('WARRIOR') ],
			inventory: {
				capacity: 40,
				contents: []
			}
		});

		const cs = state.subState('crafters');
		this._crafters = state.current.crafters.map((_, index) => new Crafter(this, cs.subState(index)));

		const hs = state.subState('heroes');
		this._heroes = state.current.heroes.map((_, index) => new Hero(this, hs.subState(index)));

		this.inventory = new Inventory(
			state.subState('inventory')
		);
	}


	public get crafters(): readonly Crafter[] {
		return this._crafters;
	}

	public get heroes(): readonly Hero[] {
		return this._heroes;
	}


	public recruitHero(key: string) {
		const { heroes } = this.state.current;
		if (heroes.length === MAX_HEROES) {
			throw new PlayerError(PlayerErrorKind.RECRUITMENT_LIMIT_REACHED);
		}

		this.state.patch({
			heroes: [
				...heroes,
				Hero.create(key)
			]
		});

		this._heroes.push(new Hero(
			this,
			this.state
				.subState('heroes')
				.subState(heroes.length)
		));
	}

	public recruitCrafter(key: string) {
		const { crafters } = this.state.current;
		if (crafters.some(crafter => crafter.key === key)) {
			throw new PlayerError(PlayerErrorKind.CRAFTER_ALREADY_RECRUITED);
		}

		this.state.patch({
			crafters: [
				...crafters,
				Crafter.create(key)
			]
		});

		this._crafters.push(new Crafter(
			this,
			this.state
				.subState('crafters')
				.subState(crafters.length)
		));
	}
}
