import { first } from '~/utils/array';

import { Combatant, CombatSide, Participants } from './Combatant';

/*
currently:
heroes:		enemies:
0			0

future:
heroes:		enemies:
3, 0,		0, 3,
4, 1,		1, 4,
5, 2		2, 5
*/

interface SingleTargetFunction {
	(attacker: Combatant, participants: Participants): Combatant;
}

// interface MultiTargetFunction {
// 	(attacker: Combatant, participants: Participants): readonly Combatant[];
// }

export const directEnemy: SingleTargetFunction = (attacker, participants) => (
	first(getEnemies(attacker, participants))!
);

