function getFriendly(attacker: Combatant, participants: Participants) {
	switch (attacker.side) {
		case CombatSide.GOOD:
			return participants.good;

		case CombatSide.EVIL:
			return participants.evil;
	}
}

function getEnemies(attacker: Combatant, participants: Participants) {
	switch (attacker.side) {
		case CombatSide.GOOD:
			return participants.evil;

		case CombatSide.EVIL:
			return participants.good;
	}
}
