import { useIntl } from '@calmdownval/intl-preact';
import { h } from 'preact';

import { useRouter } from '~/components/AppRouter';
import { Button } from '~/components/Button';
import { ButtonGroup } from '~/components/ButtonGroup';
import { EntryCard } from '~/components/EntryCard';
import { FlatLayout } from '~/components/FlatLayout';
import { Crafter, CrafterCraftReadyActivity } from '~/game/entities/Crafter';
import { ItemKinds } from '~/game/items/ItemKind';
import * as Route from '~/routes';
import { enumGet } from '~/utils/enum';

import { ScreenType } from '../ScreenType';

interface CraftingEndScreenParams {
	readonly crafter: Crafter;
}

export const CraftingEndScreen: ScreenType = () => {
	const { t } = useIntl();
	const router = useRouter<CraftingEndScreenParams>();
	const { crafter } = router.params;
	const { loot } = crafter.activity as CrafterCraftReadyActivity;

	return (
		<FlatLayout>
			<EntryCard
				icon={enumGet(ItemKinds, loot.itemKey).icon}
				quantity={loot.quantity}
				title={t(`item.${loot.itemKey}`)}
			/>
			<ButtonGroup>
				<Button
					onClick={() => {
						crafter.deliverCraft();
						router.navigate(Route.CRAFTERS);
					}}
				>
					{t('ui.label.end-task')}
				</Button>
			</ButtonGroup>
		</FlatLayout>
	);
};
