/**
 * Clamps the given value between min and max. Both limits are *inclusive*.
 */
export function clamp(value: number, min = 0.0, max = 1.0) {
	return value > max ? max : value < min ? min : value;
}

/**
 * Applies linear interpolation onto the given value so that its <0; 1> range
 * is projected onto <start; end>. Values may go beyond these ranges.
 */
export function interpolate(value: number, start: number, end: number) {
	return (end - start) * value + start;
}

/**
 * Returns a random floating point value between min (inclusive) and max
 * (exclusive).
 */
export function randomFloat(min: number, max: number) {
	return interpolate(Math.random(), min, max);
}

/**
 * Returns a random integer value between min (inclusive) and max (exclusive).
 */
export function randomInt(min: number, max: number) {
	return Math.trunc(randomFloat(min, max));
}

/**
 * Returns one of the provided items at random.
 */
export function choose<T>(item0: T, item1: T, ...moreItems: T[]): T;
export function choose<T>(...items: T[]) {
	return items[randomInt(0, items.length)];
}
