import { useIntl } from '@calmdownval/intl-preact';
import { h } from 'preact';
import { useState } from 'preact/hooks';

import { useRouter } from '~/components/AppRouter';
import { Button } from '~/components/Button';
import { EntryCard } from '~/components/EntryCard';
import { FlatLayout } from '~/components/FlatLayout';
import { Menu } from '~/components/Menu';
import { MainHeader } from '~/components/Typography';
import { Crafter, Recipe } from '~/game/entities/Crafter';
import { ItemKinds } from '~/game/items/ItemKind';
import * as Route from '~/routes';
import { ScreenType } from '~/screens/ScreenType';
import { min } from '~/utils/array';
import { enumGet } from '~/utils/enum';

import './CraftingRecipeScreen.css';

interface CraftingRecipeScreenParams {
	readonly crafter: Crafter;
	readonly recipe: Recipe;
}

export const CraftingRecipeScreen: ScreenType = ({ game }) => {
	const { t } = useIntl();
	const router = useRouter<CraftingRecipeScreenParams>();
	const [ quantity, setQuantity ] = useState(1);
	const { crafter, recipe } = router.params;

	const header = (
		<MainHeader>
			{t('ui.title.craft-item', { item: t(`item.${recipe.result.itemKey}`) })}
		</MainHeader>
	);

	const { maxStack } = enumGet(ItemKinds, recipe.result.itemKey);
	const maxQuantity = Math.min(maxStack, Math.floor(min(recipe.materials, material => (
		game.inventory.count(material.itemKey) / material.quantity
	))));

	const decreaseDisabled = maxQuantity <= 1 || quantity === 1;
	const increaseDisabled = maxQuantity <= 1 || quantity === maxQuantity;

	return (
		<FlatLayout>
			<Menu
				header={header}
				items={recipe.materials}
				itemRenderer={material => (
					<EntryCard
						icon={enumGet(ItemKinds, material.itemKey).icon}
						quantity={material.quantity * quantity}
						title={t(`item.${material.itemKey}`)}
					/>
				)}
			/>
			<div class='crafting-recipe__controls'>
				<Button
					class='crafting-recipe__quantity-min'
					disabled={decreaseDisabled}
					onClick={() => setQuantity(1)}
				>
					{t('ui.label.minimum')}
				</Button>
				<Button
					class='crafting-recipe__quantity-decrease'
					disabled={decreaseDisabled}
					onClick={() => setQuantity(quantity - 1)}
				>
					{t('ui.label.minus')}
				</Button>
				<span class='crafting-recipe__quantity-value'>
					{quantity}
				</span>
				<Button
					class='crafting-recipe__quantity-increase'
					disabled={increaseDisabled}
					onClick={() => setQuantity(quantity + 1)}
				>
					{t('ui.label.plus')}
				</Button>
				<Button
					class='crafting-recipe__quantity-max'
					disabled={increaseDisabled}
					onClick={() => setQuantity(maxQuantity)}
				>
					{t('ui.label.maximum')}
				</Button>
				<Button
					class='crafting-recipe__start-button'
					disabled={maxQuantity === 0}
					onClick={() => {
						crafter.scheduleCraft(recipe, quantity);
						router.navigate(Route.CRAFTERS);
					}}
				>
					{t('ui.label.start-task')}
				</Button>
			</div>
		</FlatLayout>
	);
};
