import { useIntl } from '@calmdownval/intl-preact';
import { h } from 'preact';

import { useRouter } from '~/components/AppRouter';
import { FlatLayout } from '~/components/FlatLayout';
import { Menu } from '~/components/Menu';
import { MainHeader } from '~/components/Typography';
import { CrafterActivity, CrafterCraftPendingActivity } from '~/game/entities/Crafter';
import { useScheduledUpdates } from '~/hooks/useScheduledUpdates';
import * as Route from '~/routes';
import type { ScreenType } from '~/screens/ScreenType';

import { renderCrafterEntry } from './entries';

export const CraftersScreen: ScreenType = ({ game }) => {
	const { t } = useIntl();
	const router = useRouter();

	// refresh this screen when a quest finishes
	useScheduledUpdates(
		game.scheduler,
		game.crafters
			.filter(crafter => crafter.activity.kind === CrafterActivity.CRAFT_PENDING)
			.map(crafter => (crafter.activity as CrafterCraftPendingActivity).elapsesAt)
	);

	const header = (
		<MainHeader>
			{t('ui.title.crafters')}
		</MainHeader>
	);

	return (
		<FlatLayout>
			<Menu
				header={header}
				items={game.crafters}
				itemRenderer={crafter => renderCrafterEntry({
					game,
					crafter,
					router,
					t,
					icon: crafter.icon,
					title: t(`entity.${crafter.key}`),
					quantity: crafter.level.value
				})}
			/>
		</FlatLayout>
	);
};
