import { CrafterActivity } from '~/game/entities/Crafter';

import { CrafterCraftPendingEntryRenderer } from './CrafterCraftPendingEntryRenderer';
import { CrafterCraftReadyEntryRenderer } from './CrafterCraftReadyEntryRenderer';
import { CrafterIdleEntryRenderer } from './CrafterIdleEntryRenderer';
import type { CrafterEntryRenderer, CrafterEntryRendererArgs } from './types';

const activityMap: Record<CrafterActivity, CrafterEntryRenderer<any>> = {
	[CrafterActivity.IDLE]: CrafterIdleEntryRenderer,
	[CrafterActivity.CRAFT_PENDING]: CrafterCraftPendingEntryRenderer,
	[CrafterActivity.CRAFT_READY]: CrafterCraftReadyEntryRenderer,
};

export const renderCrafterEntry = (args: CrafterEntryRendererArgs) => (
	activityMap[args.crafter.activity.kind](args.crafter.activity, args)
);
