import { useIntl } from '@calmdownval/intl-preact';
import { h } from 'preact';

import { useRouter } from '~/components/AppRouter';
import { EntryCard } from '~/components/EntryCard';
import { FlatLayout } from '~/components/FlatLayout';
import { Menu } from '~/components/Menu';
import { MainHeader } from '~/components/Typography';
import type { Crafter } from '~/game/entities/Crafter';
import { ItemKinds } from '~/game/items/ItemKind';
import * as Route from '~/routes';
import type { ScreenType } from '~/screens/ScreenType';
import { enumGet } from '~/utils/enum';

interface CraftingListScreenParams {
	readonly crafter: Crafter;
}

export const CraftingListScreen: ScreenType = () => {
	const { t } = useIntl();
	const router = useRouter<CraftingListScreenParams>();
	const { crafter } = router.params;

	const header = (
		<MainHeader>
			{t('ui.title.recipes')}
		</MainHeader>
	);

	return (
		<FlatLayout>
			<Menu
				header={header}
				items={crafter.recipes}
				itemRenderer={recipe => (
					<EntryCard
						icon={enumGet(ItemKinds, recipe.result.itemKey).icon}
						title={t(`item.${recipe.result.itemKey}`)}
						actionDisabled={crafter.level.value < recipe.minLevel}
						action={() => {
							router.navigate(
								Route.CRAFTING_RECIPE,
								{
									crafter,
									recipe
								}
							);
						}}
					/>
				)}
			/>
		</FlatLayout>
	);
};
