import { sum } from '~/utils/array';
import { randomFloat, randomInt } from '~/utils/math';

import { ItemStack } from './ItemStack';

export interface LootTableItem {
	readonly itemKey: string;
	readonly maxQuantity: number;
	readonly minQuantity: number;
}

export interface LootTableRow {
	readonly items: readonly LootTableItem[];
	readonly probability: number;
}

export class LootTable {
	public constructor(
		private readonly rows: readonly LootTableRow[]
	) {}

	public generateLoot(): ItemStack[] {
		const row = this.chooseRow();
		const items: ItemStack[] = [];

		for (const item of row.items) {
			const quantity = randomInt(item.minQuantity, item.maxQuantity + 1);
			if (quantity > 0) {
				items.push({
					itemKey: item.itemKey,
					quantity
				});
			}
		}

		return items;
	}

	private chooseRow() {
		const probabilitySum = sum(this.rows, row => row.probability);

		let chance = randomFloat(0, probabilitySum);
		for (const row of this.rows) {
			chance -= row.probability;
			if (chance <= 0) {
				return row;
			}
		}

		return LootTable.EMPTY_ROW;
	}

	private static readonly EMPTY_ROW: LootTableRow = {
		items: [],
		probability: 0
	};

	public static generateLoot(rows: readonly LootTableRow[]) {
		return new LootTable(rows).generateLoot();
	}
}
