import type { ItemStack } from '~/game/items/ItemStack';
import { Registry, RegistryEntry } from '~/game/Registry';
import type { State } from '~/game/storage/State';
import type { TraderGame } from '~/game/TraderGame';

import { Level } from './Level';


export enum CrafterActivity {
	IDLE,
	CRAFT_PENDING,
	CRAFT_READY
}

export interface CrafterActivityBase<TActivity extends CrafterActivity> {
	readonly kind: TActivity;
}

export interface CrafterIdleActivity extends CrafterActivityBase<CrafterActivity.IDLE> {}

export interface CrafterCraftPendingActivity extends CrafterActivityBase<CrafterActivity.CRAFT_PENDING> {
	readonly elapsesAt: number;
	readonly recipeIndex: number;
	readonly quantity: number;
}

export interface CrafterCraftReadyActivity extends CrafterActivityBase<CrafterActivity.CRAFT_READY> {
	readonly loot: ItemStack;
	readonly xp: number;
}


export interface Recipe {
	readonly baseDurationMs: number;
	readonly minLevel: number;
	readonly materials: readonly ItemStack[];
	readonly result: ItemStack;
}

export interface CrafterInfo extends RegistryEntry {
	readonly icon: string;
	readonly recipes: readonly Recipe[];
}

export interface CrafterState {
	readonly key: string;
	readonly xp: number;
	readonly activity:
	| CrafterIdleActivity
	| CrafterCraftPendingActivity
	| CrafterCraftReadyActivity;
}


export class Crafter {
	public readonly level: Level;

	public constructor(
		private readonly game: TraderGame,
		private readonly state: State<CrafterState>
	) {
		this.level = new Level(
			state.subState('xp')
		);
	}


	public get activity() {
		return this.state.current.activity;
	}

	private get entry() {
		return Crafter.registry.get(this.key);
	}

	public get icon() {
		return this.entry.icon;
	}

	public get key() {
		return this.state.current.key;
	}

	public get recipes() {
		return this.entry.recipes;
	}


	public scheduleCraft(recipe: Recipe, quantity: number) {
		if (this.activity.kind !== CrafterActivity.IDLE) {
			throw new Error('Cannot schedule a craft when the crafter is not IDLE.');
		}

		const recipeIndex = this.entry.recipes.indexOf(recipe);
		if (recipeIndex === -1) {
			throw new Error('Crafter can only craft its own recipes.');
		}

		this.game.inventory.remove(recipe.materials.map(material => ({
			...material,
			quantity: material.quantity * quantity
		})));

		this.state.patch({
			activity: {
				kind: CrafterActivity.CRAFT_PENDING,
				elapsesAt: this.game.clock.now + recipe.baseDurationMs * quantity,
				recipeIndex,
				quantity
			}
		});
	}

	public completeCraft() {
		if (this.activity.kind !== CrafterActivity.CRAFT_PENDING || this.activity.elapsesAt > this.game.clock.now) {
			throw new Error('Cannot complete a craft when it did not elapse.');
		}

		const { result } = this.entry.recipes[this.activity.recipeIndex];
		this.state.patch({
			activity: {
				kind: CrafterActivity.CRAFT_READY,
				loot: {
					...result,
					quantity: result.quantity * this.activity.quantity,
				},
				xp: 0 * this.activity.quantity // TODO
			}
		});
	}

	public deliverCraft() {
		if (this.activity.kind !== CrafterActivity.CRAFT_READY) {
			throw new Error('Cannot deliver a craft when not ready.');
		}

		this.game.inventory.add([ this.activity.loot ]);
		this.level.xp += this.activity.xp;
		this.state.patch({
			activity: {
				kind: CrafterActivity.IDLE
			}
		});
	}


	public static readonly registry = new Registry<CrafterInfo>();

	public static create(key: string): CrafterState {
		return {
			key,
			xp: 0,
			activity: {
				kind: CrafterActivity.IDLE
			}
		};
	}
}
