import { XP_LOG_BASE, XP_SCALE } from '~/game/constants';
import type { State } from '~/game/storage/State';

export class Level {
	private _value?: number;

	public constructor(
		private readonly state: State<number>
	) {}


	public get xp() {
		return this.state.current;
	}

	public set xp(value: number) {
		if (Number.isFinite(value) && value >= 0) {
			this.state.set(value);
			this._value = undefined;
		}
	}

	public get value() {
		return (this._value ??= Level.getLevelByExperience(this.xp));
	}

	public get xpRequiredForNextLevel() {
		return Level.getExperienceRequiredForLevel(this.value + 1) - Level.getExperienceRequiredForLevel(this.value);
	}

	public get xpThisLevel() {
		return this.xp - Level.getExperienceRequiredForLevel(this.value);
	}


	public static getExperienceRequiredForLevel(level: number) {
		return Math.ceil(
			XP_SCALE * (XP_LOG_BASE ** (Math.floor(level) - 1) - 1)
		);
	}

	public static getLevelByExperience(experience: number) {
		return 1 + Math.floor(
			Math.log(
				1 + experience / XP_SCALE
			) / Math.log(XP_LOG_BASE)
		);
	}
}
