import { h } from 'preact';
import { useLayoutEffect, useRef } from 'preact/hooks';

import { cx } from '~/utils/style';

import './ProgressBar.css';

export interface ProgressBarProps {
	readonly class?: string;
	readonly formatValue?: (value: number) => string;
	readonly hideLabel?: boolean;
	readonly maxValue: number;
	readonly title?: string;
	readonly value: number;
}

export function toIntString(value: number) {
	return Math.trunc(value).toString();
}

export const ProgressBar = ({
	class: customClass,
	formatValue = toIntString,
	hideLabel,
	maxValue,
	title,
	value
}: ProgressBarProps) => {
	const wrapperRef = useRef<HTMLElement>();
	useLayoutEffect(() => {
		wrapperRef.current?.style.setProperty('--progress', '' + (value / maxValue));
	}, [ value, maxValue ]);

	return (
		<div
			class={cx('progress-bar', customClass)}
			ref={wrapperRef as any}
		>
			<div class='progress-bar__indicator' />
			{!hideLabel && (
				<span class='progress-bar__label'>
					{`${title ? `${title}: ` : ''}${formatValue(value)} / ${formatValue(maxValue)}`}
				</span>
			)}
		</div>
	);
};
