import type { Attribute } from '~/game/entities/Attributes';
import type { AppliedEffect, Effect } from '~/game/entities/combat/Effect';

import type { LivingEntity } from './LivingEntity';

export enum CombatSide {
	GOOD,
	EVIL
}

export enum SetMode {
	CONST,
	OFFSET,
	MULTIPLIER
}

export interface Combatant {
	readonly entity: LivingEntity;
	readonly effects: readonly AppliedEffect[];
	readonly isAlive: boolean;
	readonly side: CombatSide;
	readonly health: number;
	damage(value: number, defense?: Attribute): void;
	heal(value: number): void;
	addEffect(effect: Effect, param?: number): void;
	removeEffect(effect: AppliedEffect): void;
	getAttribute(attribute: Attribute): number;
	setAttribute(attribute: Attribute, value: number, mode?: SetMode): void;
	tickEffects(): void;
}

export interface Participants {
	readonly evil: readonly Combatant[];
	readonly good: readonly Combatant[];
}
