import type { StorageBackend } from './StateStorage';

export interface LocalStorageBackendArgs {
	readonly debounceMs?: number;
	readonly key: string;
	readonly storage?: Storage;
}

export class LocalStorageBackend<T> implements StorageBackend<T> {
	private readonly debounceMs: number;
	private readonly key: string;
	private readonly storage: Storage;
	private debounceHandle?: number;
	private isInitialized = false;
	private state?: T;

	public constructor(args: LocalStorageBackendArgs) {
		this.debounceMs = args.debounceMs ?? 100;
		this.key = args.key;
		this.storage = args.storage ?? window.localStorage;
	}

	public get() {
		if (!this.isInitialized) {
			const json = this.storage.getItem(this.key);
			if (!json) {
				throw new Error(`key '${this.key}' not found within the storage`);
			}

			this.state = JSON.parse(json);
			this.isInitialized = true;
		}

		return this.state!;
	}

	public set(state: any, immediate = false) {
		if (this.debounceHandle !== undefined) {
			cancelIdleCallback(this.debounceHandle);
		}

		this.state = state;
		const onSet = () => {
			this.debounceHandle = undefined;
			try {
				this.storage.setItem(this.key, JSON.stringify(state));
				this.isInitialized = true;
			}
			catch (ex) {
				// eslint-disable-next-line no-console
				console.error('Failed to store state:', ex);
			}
		};

		if (immediate) {
			onSet();
		}
		else {
			this.debounceHandle = requestIdleCallback(onSet, { timeout: this.debounceMs });
		}
	}
}
