import { h } from 'preact';

import { EntryCard } from '~/components/EntryCard';
import { ProgressBar } from '~/components/ProgressBar';
import type { HeroIdleActivity } from '~/game/entities/Hero';
import * as Route from '~/routes';

import type { HeroEntryRenderer } from './types';

export const HeroIdleEntryRenderer: HeroEntryRenderer<HeroIdleActivity> = (_activity, { hero, router, t, ...rest }) => (
	<EntryCard
		{...rest}
		action={() => router.navigate(Route.QUEST_SELECT, { hero })}
	>
		<ProgressBar
			value={hero.health.value}
			maxValue={hero.health.maxHealth}
			title={t('ui.label.hp')}
		/>
	</EntryCard>
);
