import { h } from 'preact';

import { Countdown } from '~/components/Countdown';
import { EntryCard } from '~/components/EntryCard';
import { SubHeader } from '~/components/Typography';
import type { CrafterCraftPendingActivity } from '~/game/entities/Crafter';
import * as Route from '~/routes';

import type { CrafterEntryRenderer } from './types';

export const CrafterCraftPendingEntryRenderer: CrafterEntryRenderer<CrafterCraftPendingActivity> = (activity, {
	game,
	crafter,
	router,
	t,
	...rest
}) => {
	const remainingTimeMs = activity.elapsesAt - game.clock.now;
	return (
		<EntryCard
			{...rest}
			action={() => {
				crafter.completeCraft();
				router.navigate(Route.CRAFTING_END, { crafter });
			}}
			actionDisabled={remainingTimeMs > 0}
			actionContent={remainingTimeMs > 0
				? (
					<Countdown
						scheduler={game.scheduler}
						timestamp={activity.elapsesAt}
					/>
				)
				: undefined
			}
		>
			<SubHeader>
				{t(`ui.crafter-state.${remainingTimeMs > 0 ? 'CRAFT_PENDING' : 'CRAFT_READY'}`)}
			</SubHeader>
		</EntryCard>
	);
};
