import { Attribute } from '~/game/entities/Attributes';
import { enumOf, EnumType } from '~/utils/enum';
import { randomFloat } from '~/utils/math';

import type { Combatant, Participants } from './Combatant';
import { Effects } from './Effect';
import { directEnemy } from './targeting';

export interface Attack extends EnumType {
	execute(this: Attack, attacker: Combatant, participants: Participants): void;
}

// we want each entity to have a set rotation of three attacks
export type AttackRotation = readonly [ string, string, string ];

export const Attacks = enumOf<Attack>()({
	NONE: {
		execute() {
			// do nothing
		}
	},
	LUNGE: {
		execute(attacker, participants) {
			const defender = directEnemy(attacker, participants);
			const damage = attacker.getAttribute(Attribute.STRENGTH) * randomFloat(0.9, 1.1);

			defender.damage(damage, Attribute.PHYSICAL_DEFENSE);
		}
	},
	FIREBALL: {
		execute(attacker, participants) {
			const defender = directEnemy(attacker, participants);
			const damage = attacker.getAttribute(Attribute.INTELLECT) * randomFloat(0.9, 1.1);

			defender.damage(damage, Attribute.MAGIC_DEFENSE);
			defender.addEffect(Effects.IGNITE, damage);
		}
	},
	SUNDER: {
		execute(attacker, participants) {
			const defender = directEnemy(attacker, participants);
			defender.addEffect(Effects.SUNDER);
		}
	}
});
