import { useIntl } from '@calmdownval/intl-preact';
import { h } from 'preact';

import { useRouter } from '~/components/AppRouter';
import { Button } from '~/components/Button';
import { FlatLayout } from '~/components/FlatLayout';
import { Menu } from '~/components/Menu';
import { MainHeader } from '~/components/Typography';
import { MAX_HEROES } from '~/game/constants';
import { HeroActivity, HeroQuestPendingActivity } from '~/game/entities/Hero';
import { useScheduledUpdates } from '~/hooks/useScheduledUpdates';
import * as Route from '~/routes';
import type { ScreenType } from '~/screens/ScreenType';

import { renderHeroEntry } from './entries';

export const HeroesScreen: ScreenType = ({ game }) => {
	const { t } = useIntl();
	const router = useRouter();

	// refresh this screen when a quest finishes
	useScheduledUpdates(
		game.scheduler,
		game.heroes
			.filter(hero => hero.activity.kind === HeroActivity.QUEST_PENDING)
			.map(hero => (hero.activity as HeroQuestPendingActivity).elapsesAt)
	);

	const header = (
		<MainHeader>
			{t('ui.title.heroes')}
		</MainHeader>
	);

	const footer = (
		<Button
			disabled={game.heroes.length >= MAX_HEROES}
			onClick={() => router.navigate(Route.HERO_RECRUIT)}
		>
			{t('ui.label.recruit-hero')}
		</Button>
	);

	return (
		<FlatLayout>
			<Menu
				header={header}
				footer={footer}
				items={game.heroes}
				itemRenderer={hero => renderHeroEntry({
					game,
					hero,
					router,
					t,
					icon: hero.icon,
					title: t(`entity.${hero.key}`),
					quantity: hero.level.value,
					secondaryAction: () => router.navigate(Route.HERO_EDIT, { hero })
				})}
			/>
		</FlatLayout>
	);
};
