import { useIntl } from '@calmdownval/intl-preact';
import { h } from 'preact';

import { useRouter } from '~/components/AppRouter';
import { EntryCard } from '~/components/EntryCard';
import { Menu } from '~/components/Menu';
import { MainHeader } from '~/components/Typography';
import { Hero } from '~/game/entities/Hero';
import { EquipmentSlot } from '~/game/items/EquipmentSlot';
import { ItemKinds } from '~/game/items/ItemKind';
import * as Route from '~/routes';
import { enumGet } from '~/utils/enum';

import { ScreenType } from '../ScreenType';

export interface HeroEquipItemParams {
	hero: Hero;
	slot: EquipmentSlot;
}

export const HeroEquipItem: ScreenType = ({ game }) => {
	const { t } = useIntl();
	const router = useRouter<HeroEquipItemParams>();
	const { hero, slot } = router.params;

	const header = (
		<header class='hero-equip-item__header'>
			<MainHeader>
				{t('ui.title.hero-equip-item')}
				<br />
				{t(`entity.${hero.key}`)}
			</MainHeader>
		</header>
	);

	const available = game.inventory.contents.filter(stack => enumGet(ItemKinds, stack.itemKey).slot === slot);
	return (
		<Menu
			header={header}
			items={available}
			itemRenderer={stack => (
				<EntryCard
					icon={enumGet(ItemKinds, stack.itemKey).icon}
					title={t(`item.${stack.itemKey}`)}
					action={() => {
						hero.equip(stack.itemKey);
						router.navigate(Route.HERO_EDIT, { hero });
					}}
				/>
			)}
		/>
	);
};
