import { HeroActivity } from '~/game/entities/Hero';

import { HeroDeadEntryRenderer } from './HeroDeadEntryRenderer';
import { HeroIdleEntryRenderer } from './HeroIdleEntryRenderer';
import { HeroQuestPendingEntryRenderer } from './HeroQuestPendingEntryRenderer';
import { HeroQuestReadyEntryRenderer } from './HeroQuestReadyEntryRenderer';
import type { HeroEntryRenderer, HeroEntryRendererArgs } from './types';

const activityMap: Record<HeroActivity, HeroEntryRenderer<any>> = {
	[HeroActivity.DEAD]: HeroDeadEntryRenderer,
	[HeroActivity.IDLE]: HeroIdleEntryRenderer,
	[HeroActivity.QUEST_PENDING]: HeroQuestPendingEntryRenderer,
	[HeroActivity.QUEST_READY]: HeroQuestReadyEntryRenderer
};

export const renderHeroEntry = (args: HeroEntryRendererArgs) => (
	activityMap[args.hero.activity.kind](args.hero.activity, args)
);
