import { h } from 'preact';

import { EntryCard } from '~/components/EntryCard';
import { SubHeader } from '~/components/Typography';
import type { CrafterCraftReadyActivity } from '~/game/entities/Crafter';
import * as Route from '~/routes';

import type { CrafterEntryRenderer } from './types';

export const CrafterCraftReadyEntryRenderer: CrafterEntryRenderer<CrafterCraftReadyActivity> = (_activity, {
	crafter,
	router,
	t,
	...rest
}) => (
	<EntryCard
		{...rest}
		action={() => router.navigate(Route.CRAFTING_END, { crafter })}
	>
		<SubHeader>
			{t('ui.crafter-state.CRAFT_READY')}
		</SubHeader>
	</EntryCard>
);
