import { Phrase, useIntl } from '@calmdownval/intl-preact';
import { h } from 'preact';

import type { Scheduler } from '~/game/time/Scheduler';
import { usePeriodicUpdates } from '~/hooks/usePeriodicUpdates';

export interface CountdownProps {
	readonly scheduler: Scheduler;
	readonly timestamp: number;
}

export const Countdown = ({ scheduler, timestamp }: CountdownProps) => {
	const { t } = useIntl();
	const remainingMs = timestamp - scheduler.clock.now;
	usePeriodicUpdates(scheduler, remainingMs > 0 ? 1000 : undefined);

	return (
		<span class='countdown'>
			{t(formatTimeRemainder(remainingMs))}
		</span>
	);
};

const units = [
	{
		key: 'ui.time.days',
		ms: 86_400_000
	},
	{
		key: 'ui.time.hours',
		ms: 3_600_000
	},
	{
		key: 'ui.time.minutes',
		ms: 60_000
	},
	{
		key: 'ui.time.seconds',
		ms: 1_000
	}
];

function formatTimeRemainder(remainingMs: number): Phrase {
	for (const unit of units) {
		const remaining = Math.floor(remainingMs / unit.ms);
		if (remaining >= 1) {
			return {
				key: unit.key,
				vars: { remaining }
			};
		}
	}

	return { text: '' };
}
